; (function () {
    let forms = document.querySelectorAll('.more__form');

    if (forms.length === 0) {
        return;
    }

    let serialize = function (form) {
        let items = form.querySelectorAll('input, select, textarea');
        let str = '';
        for (let i = 0; i < items.length; i += 1) {
            let item = items[i];
            let name = item.name;
            let value = item.value;
            let separator = i === 0 ? '' : '&';

            if (name) {
                str += separator + name + '=' + encodeURI(value);
            }
        }
        return str;
    }

    let formSend = function (form) {
        let data = serialize(form);
        let xhr = new XMLHttpRequest();
        let url = './mail.php';

        xhr.open('POST', url);
        xhr.setRequestHeader('Content-Tyope', 'application/x-www-form-urlencoded');

        xhr.onload = function () {

            if (xhr.response === 'success') {
                document.querySelector('.popup-send').classList.add('is-active');
            } else {
                document.querySelector('.popup-error').classList.add('is-active');
            }
            form.reset();
        };

        xhr.send(data)
    };

    for (let i = 0; i < forms.length; i += 1) {
        forms[i].addEventListener('submit', function (e) {
            e.preventDefault();
            let form = e.currentTarget;
            formSend(form);
        });
    }

})();


const body = document.querySelector('.page__body');
const popupError = document.querySelector('.popup-error');
const popupContent = document.querySelector('.popup-content');

const observer = new MutationObserver(function(mutationsList, observer) {
    for(let mutation of mutationsList) {
        if (mutation.type === 'attributes' && mutation.attributeName === 'class') {
            const isActive = popupError.classList.contains('is-active');
            body.classList.toggle('disable-scroll', isActive);
        }
    }
});

observer.observe(popupError, { attributes: true });

document.addEventListener('DOMContentLoaded', function() {
    const isActive = popupError.classList.contains('is-active');
    body.classList.toggle('disable-scroll', isActive);
});


document.addEventListener('click', function(evt) {
    const isClick = evt.target.closest('.popup-content');

    if (isClick) {
        return;
    }
    popupError.classList.remove('is-active');
});

const closeBtn = document.querySelector('.popup-close');

closeBtn.addEventListener('click', function() {
    const popup = this.closest('.poup');
    popup.classList.remove('is-active');
});